@import './var.scss';

.lc-info {
  margin-top: 8px;
  padding: 18px 16px;
  background: #eee8fc;
  border-radius: 5px;

  > p {
    display: flex;
    justify-content: space-between;
    margin: 0 0 12px 0;

    &:last-child {
      margin: 0;
    }

    > span {
      font-size: 12px;
      color: #5915e1;

      .opa-5 {
        opacity: 0.5;
      }

      &.opa-5 {
        opacity: 0.5;
      }

      &:last-child {
        font-weight: 500;
        font-size: 14px;
        color: #5915e1;
        opacity: 1;
      }
    }
  }
}

.lc-date {
  .lc-date-eles {
    display: flex;
    flex-flow: wrap;

    > span {
      display: inline-block;
      min-width: 50px;
      padding: 0 5px;
      line-height: 30px;
      text-align: center;
      border: 1px solid #e1e1e1;
      margin: 0 10px 10px 0;
      border-radius: 5px;
      font-size: 12px;
      color: #333;
      cursor: pointer;

      &.current {
        border-color: #5915e1;
        color: #5915e1;
      }
    }
  }
}

.max-btn {
  display: inline-block;
  cursor: pointer;
}

.line {
  width: 100%;
  height: 1px;
  background: #e5e5e5;
  opacity: 0.5;
  margin: 16px 0;
}

.common-btn {
  width: 100%;
  line-height: 40px;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: -0.3px;
  color: #fff;
  text-align: center;
  background: #5915e1;
  border-radius: 5px !important;
  border: none;
  box-shadow: none;
  cursor: pointer;
  margin-top: 20px;

  &:disabled {
    opacity: 0.3;
    cursor: not-allowed !important;
  }

  &.mt-ers {
    margin-top: 60px;
  }

  &.border {
    border: 1px solid #5915e1 !important;
    color: #5915e1 !important;
    background: #fff !important;

    &:disabled {
      color: #b79cef !important;
      cursor: not-allowed !important;
      border-color: #b79cef !important;
    }
  }
}

.common-error {
  height: 32px;
  line-height: 32px;
  padding: 0 10px;
  margin-top: 8px;
  background: #f8f7c8;
  color: #333;
  text-align: left;
  border-radius: 5px;
  font-size: 12px;
}

.jcfe {
  justify-content: flex-end !important;
}

.ais {
  align-items: flex-start !important;
}

.cp {
  cursor: pointer;
}

.vest-table.ant-table-wrapper {
  th {
    font-size: 14px !important;
    color: #999 !important;
    font-weight: normal !important;
    background-color: #fff !important;
    padding-top: 0 !important;
    padding-bottom: 12px !important;
  }

  td {
    color: #333;
    border: none !important;
  }

  .ant-table table th,
  .ant-table table td {
    line-height: 20px;
    width: 33.33%;
    padding: 8px 6px !important;
    font-size: 12px;
    text-align: left;
  }
}

.lock-table {
  th,
  td {
    width: 50%;
  }
}

.mt-6 {
  margin-top: 6px;
}

.mt-8 {
  margin-top: 8px;
}

.mt-10 {
  margin-top: 10px;
}

.mt-12 {
  margin-top: 12px !important;
}

.mt-16 {
  margin-top: 16px !important;
}

.mt-24 {
  margin-top: 24px !important;
}

.mt-30 {
  margin-top: 30px !important;
}

.ml-2 {
  margin-left: 2px !important;
}

.mr-5 {
  margin-right: 5px !important;
}

.mr-8 {
  margin-right: 8px !important;
}

.mb-0 {
  margin-bottom: 0px !important;
}

.mb-6 {
  margin-bottom: 6px;
}

.mb-8 {
  margin-bottom: 8px;
}

.mb-10 {
  margin-bottom: 10px;
}

div.tac {
  margin: 0;
}

.lh-12 {
  line-height: 12px;
}

.lh-22 {
  line-height: 22px;
}

.fw-500 {
  font-weight: 500;
}

.c-333 {
  color: #333;
}

.c-666 {
  color: #666;
}

.c-999 {
  color: #999;
}

.fs-12 {
  font-size: 12px;
}

.fs-14 {
  font-size: 14px;
}

.tooltip-img {
  width: 14px;
  height: 14px;
}

.stake-tooltip {
  .ant-tooltip-inner {
    padding: 16px;
    border-radius: 10px;
  }
}

.tooltip-img {
  width: 18px;
  height: 18px;
}

.no-data {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 310px;
  text-align: center;
  background: rgba(56, 51, 72, 0.9);
  border-radius: 15px;
  margin-bottom: 100px;

  .no-data-spin {
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
    animation: stakeRotate 1s infinite linear;
  }

  .no-data-img {
    width: 106px;
    height: 80px;
    margin-bottom: 10px;
  }
  > div {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #fff;
  }
}

.line-gray {
  height: 1px;
  background: rgba(229, 229, 229, 0.5);
}

.lock-extend-content {
  margin-top: 10px;
  .line-gray {
    margin: 10px 0 20px;
  }
}
.sun3-add-liq {
  .line-gray {
    margin: 10px 0 20px;
  }
}

.lock-amount {
  font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
    'WenQuanYi Micro Hei', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  color: #333333;
  margin-bottom: 8px;
  .balance {
    color: #999999;
  }
}

.lock-price {
  margin-top: 8px;
  > span {
    font-family: 'Helvetica Neue', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #999999;
  }
  > a {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    text-align: right;
    color: #5915e1;
  }
  .error {
    color: #ed635a;
  }
}

.extend-check {
  margin: 20px 0;
  .ant-checkbox-inner {
    border: 1px solid #5915e1 !important;
  }
  .ant-checkbox-wrapper {
    > span {
      font-size: 12px;
    }
  }
}

.ant-checkbox-input:focus + .ant-checkbox-inner {
  border: 1px solid #5915e1 !important;
}

.ant-input-affix-wrapper {
  height: 40px;
  line-height: 40px;
  border: 1px solid #5915e1;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 4px 16px;

  .max-btn {
    min-width: 48px;
    height: 24px;
    line-height: 24px;
    background: #5915e1;
    border-radius: 5px;
    font-size: 12px;
    color: #fff;
    text-align: center;
  }
}

.extend-btns {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-bottom: 30px;
  > div {
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-weight: 400;
    font-size: 12px;
    color: #333333;
    border: 1px solid #e1e1e1;
    border-radius: 5px;
    white-space: nowrap;
    &:nth-child(2) {
      padding: 0 20px;
    }
    &.current {
      border: 1px solid #5915e1;
      color: #5915e1;
      cursor: pointer;
    }
    &.errors {
      border: 1px solid #ed635a;
      .ant-input {
        color: #ed635a;
      }
    }
    .ant-input-affix-wrapper {
      padding: 0;
      height: 28px;
      line-height: 28px;
      border: none;
    }
    .ant-input-prefix,
    .ant-input-suffix {
      font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
        'WenQuanYi Micro Hei', sans-serif;
      font-size: 12px;
      color: #333333;
    }
    .ant-input {
      font-family: 'Helvetica', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
        'WenQuanYi Micro Hei', sans-serif;
      font-size: 12px;
      line-height: 14px;
      color: #5915e1;
      // margin: 0 30px;
      text-align: center;

      &::placeholder {
        color: #5915e1;
        opacity: 0.3;
      }
    }
  }
  .error {
    position: absolute;
    bottom: 7px;
    right: 0;
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-size: 12px;
    color: #ed635a;
  }
}

.lock-overview {
  font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
    'WenQuanYi Micro Hei', sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  margin-bottom: 10px;
}

.overview-content {
  padding: 20px 16px;
  background: #f5f6ff;
  border-radius: 5px;
  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'PingFang HK', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #666666;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
    .red {
      color: #ed635a;
    }
  }
  .old-data,
  .new-data {
    font-family: 'Helvetica Neue', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
  }
  .old-data {
    display: inline-flex;
    align-items: center;
    color: #333;
    img {
      margin: 0 6px;
    }
    .apy {
      border-bottom: 1px dashed #333;
      cursor: pointer;
      margin-right: 2px;
    }
  }
  .new-data {
    color: #5915e1;
    .apy {
      border-bottom: 1px dashed #5915e1;
      cursor: pointer;
      margin-right: 2px;
    }
  }
}

.warning {
  margin-top: 10px;
  .warning-title {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 17px;
    color: #333333;
    margin-bottom: 3px;
    > img {
      margin-right: 6px;
    }
  }

  .warning-content {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 17px;

    color: #666666;
  }
}

.calculator-selector {
  width: 280px !important;
}

.new-stake-sub-nav {
  display: block;
  width: 100%;
  overflow-y: inherit;
  overflow-x: auto;
  white-space: nowrap;
  -ms-overflow-style: none; /* IE 10+ */
  scrollbar-width: none; /* Firefox */
  &::-webkit-scrollbar {
    display: none;
  }
  .sub-nav-item {
    margin-right: 30px;
    display: inline-block;
    &:first-child,
    &:last-child {
      font-size: 14px;
    }
    &:first-child {
      &:after {
        display: none;
      }
    }
  }
}

.ant-modal-wrap.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;
  .ant-modal {
    top: 50px;
  }
  .ant-modal-content {
    padding: 0;
  }
}

.lock-modal {
  .ant-modal-header {
    padding: 20px 20px 0 20px;
  }
  &.detail-modal {
    &.top-modal {
      margin-top: -200px;

      .ant-modal-body {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 150px;
      }

      .ant-statistic-content-value {
        color: rgba(0, 0, 0, 0.45);
        font-size: 14px;
      }

      .ant-statistic-content-suffix {
        color: rgba(0, 0, 0, 0.45);
        font-size: 14px;
      }
    }
  }

  .ant-modal-header {
    border-bottom: none;
  }

  .ant-modal-title {
    font-weight: 500;
    font-size: 14px;
    color: #333333;
    text-align: center;
    margin-top: 14px;
  }

  .ant-modal-content {
    border-radius: 15px;
  }

  .ant-modal-header {
    padding: 20px 20px 0 20px;
  }

  .ant-modal-body {
    padding-top: 20px;
  }

  .ant-tabs-nav-wrap {
    justify-content: center;

    .ant-tabs-tab {
      min-width: 100px;
      justify-content: center;

      .ant-tabs-tab-btn > span {
        font-weight: 500;
        font-size: 14px;
        color: #999;
        text-shadow: none !important;
      }

      &.ant-tabs-tab-active {
        .ant-tabs-tab-btn > span {
          color: #5915e1;
        }
      }
    }
  }

  .ant-tabs-content-holder {
    margin-top: 10px;
  }

  .ant-tabs-ink-bar {
    height: 2px;
    background: #5915e1;
    border-radius: 20px;
  }

  .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
    border-color: #5915e1;
  }
}

.lock-detail-modal {
  .ant-table-wrapper .ant-table-thead > tr > th::before {
    display: none;
  }
}

.ant-modal.new-lock-modal {
  &.header-bg-modal {
    .ant-modal-content {
      padding: 0;
    }
    .ant-modal-header {
      padding: 20px;
    }
  }
  .ant-modal-header {
    padding: 20px 20px 0 20px;
    background-image: url(../images/stake_new_lock_head.png);
    background-size: cover;
    background-repeat: no-repeat;
  }

  .ant-modal-title {
    text-align: left;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    color: #333333;
    margin: 0;
    margin-right: 30px;
  }

  .ant-input-affix-wrapper {
    padding: 4px 10px;
  }

  .ant-picker {
    background-color: #f5f6f9;
    border-radius: 5px;
    border: none;
  }

  .ant-picker-suffix img {
    vertical-align: middle;
  }

  .line {
    &:first-child {
      margin-bottom: 10px;
    }
  }

  .lc-info {
    background: #f9f9f9;
  }

  .lock-common-btn:disabled {
    color: #fff !important;
  }

  .lc-date {
    .lc-date-eles {
      > span {
        padding: 0 5px;
        margin: 0 2.6% 10px 0;
        width: 23%;

        &:last-child {
          margin-right: 0;
        }
      }
    }

    .error {
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #ed635a;
      height: 17px;
    }
  }
}

.ant-modal.no-content-modal {
  .ant-modal-content {
    padding: 0;
  }
}

.ant-modal.calculator-modal {
  .ant-modal-content {
    padding: 0;
  }
  .ant-modal-body {
    padding-top: 6px;
  }

  .ant-modal-header {
    padding: 20px;
    background-image: url(../images/stake_calculator_head.png);
    background-size: cover;
    background-repeat: no-repeat;
  }

  .ant-modal-title {
    text-align: left;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: #333333;
    margin: 0;
  }

  .ant-select:not(.ant-select-customize-input) .ant-select-selector {
    background-color: #f5f6f9;
    border-radius: 5px;
    height: 30px;
    width: 142px;
  }

  .ant-input-affix-wrapper {
    padding: 4px 10px;
  }

  .gray {
    margin-top: 12px;
  }

  section {
    border-bottom: 1px solid rgba(229, 229, 229, 0.5);
    padding: 10px 0 10px 0;

    &:nth-child(n + 4) {
      padding: 20px 0 20px 0;
    }

    &:last-child {
      padding: 20px 0 0 0;
      border: none;
    }
  }

  .number {
    max-width: 50%;
  }
}

.tips-modal {
  .ant-modal-title {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #333333;
  }

  .step {
    border-radius: 15px;
    font-size: 14px;
    line-height: 20px;
    color: #333333;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    padding: 40px 130px 20px 30px;

    &:last-child {
      margin-bottom: 0px;
    }

    .ant-btn {
      border-radius: 3px;
      padding: 5px 16px;
      font-size: 12px;
      line-height: 17px;
      // width: 80px;
      height: 26px;
      border: none;
      margin-top: 12px;
    }

    .icon {
      width: 76px;
      height: 24px;
      position: absolute;
      top: 0;
      left: 0;
      text-align: center;
      line-height: 24px;
      font-weight: 500;
      font-size: 12px;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  .step_1 {
    background-color: #fffaed;
    background-image: url(../images/stake_tips_step_bg1.png);

    .ant-btn {
      color: #b98500;
      background: linear-gradient(274.02deg, rgba(255, 167, 36, 0.5) -43.5%, rgba(243, 225, 62, 0.5) 131.93%);
      border: none;
    }

    .icon {
      color: #e7af25;
      background-image: url(../images/stake_tips_step1.png);
    }
  }

  .step_2 {
    background-color: #fff6ff;
    background-image: url(../images/stake_tips_step_bg2.png);

    .ant-btn {
      color: #a021d7;
      background: linear-gradient(133.67deg, rgba(239, 96, 250, 0.4) -7.09%, rgba(146, 93, 255, 0.4) 140.69%);
    }

    .icon {
      color: #c057d2;
      background-image: url(../images/stake_tips_step2.png);
    }
  }

  .step_3 {
    background-color: #f1f3ff;
    background-image: url(../images/stake_tips_step_bg3.png);

    .ant-btn {
      color: #ba133b;
      background: linear-gradient(323.52deg, rgba(255, 72, 190, 0.3) 2.44%, rgba(255, 71, 115, 0.3) 154.47%);
    }

    .icon {
      color: #8a88ff;
      background-image: url(../images/stake_tips_step3.png);
    }
  }
}

.v3-stake-token {
  .common-btn:disabled {
    color: #fff !important;
    opacity: 0.3;
  }

  .ant-modal-header {
    padding: 20px 20px 0 20px;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .ant-modal-title {
    text-align: left;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    margin: 0;
  }

  .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
    border-color: #5915e1;
  }

  .ant-input-affix-wrapper {
    padding: 4px 10px;
  }
  a {
    font-size: 12px;
    font-weight: 600;
    color: #8c51ff;
    cursor: pointer;
    &:link,
    &:visited {
      color: #8c51ff;
    }
    &:hover {
      color: rgba(#8c51ff, 0.7);
    }
  }
}

.v3-thrpoollp {
  .ant-modal-header {
    background-image: url(../images/stake_3pool_head.png);
  }
}

.v3-sunlp {
  .ant-modal-header {
    background-image: url(../images/stake_sun_trx_head.png);
  }
}

.v3-thrsunlp {
  .ant-modal-header {
    background-image: url(../images/stake_usdc_head.png);
  }
}

@media screen and (max-width: 1300px) {
  .wrap,
  .banner.pool-config {
    width: 90% !important;
  }
}

@media screen and (max-width: 600px) {
  .lock-modal {
    top: 60px;
  }
}

@media screen and (max-width: 500px) {
  .vertical-center-modal .ant-modal {
    top: 0;
  }

  .tips-modal {
    .step {
      height: 150px;
      padding: 35px 90px 20px 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;

      &.step_1 {
        background-image: url(../images/stake_tips_step_bg_m1.png);
      }

      &.step_2 {
        padding: 30px 93px 20px 20px;
        background-image: url(../images/stake_tips_step_bg_m2.png);
      }

      &.step_3 {
        background-image: url(../images/stake_tips_step_bg_m3.png);
      }

      .ant-btn {
        margin-top: 5px;
      }
    }
  }
}

